
.small-pics {
    width: 15.625vw;
    height: 27.78vh;
    position: absolute;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.small-pics:hover {
    -webkit-transform: scale(1.133);
            transform: scale(1.133);
    position: absolute;
    -webkit-box-shadow: 0 6px 20px #222222;
            box-shadow: 0 6px 20px #222222
}
.image {
    width: 100%;
    height: 100%;
}
.small-pics .pics-information {
    width: 100%;
    height: 7.407vh;
    position: absolute;
    bottom: 0;
    left: 0;
    display: none;
    z-index: 2;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.small-pics:hover .pics-information {
    display: block;
}



