
.first-pic {
    width: 33.33vw;
    height: 59.26vh;
    position: absolute;
    top: 20.37vh;
    left: 6.25vw;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.first-pic:hover {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
    position: absolute;
    -webkit-box-shadow: 0 30px 80px #303030;
            box-shadow: 0 30px 80px #303030;
}
.image {
    width: 100%;
    height: 100%;
}
.first-pic .pics-information {
    width: 100%;
    height: 9.259vh;
    position: absolute;
    bottom: 0;
    left: 0;
    display: none;    
    z-index: 2; 
    -webkit-transition: all 0.5s ease; 
    transition: all 0.5s ease;
}
.first-pic:hover .pics-information {
    display: block;
}



