
#LogoutConfirm {
    width: 20vw;
    height: 25vh;
    position: absolute;
    top: 30vh;
    left: 40vw;
    z-index: 100;
    background: url("../assets/user/bt_bg3.png") no-repeat center center;
    font-family: font757;
    font-size: 30px;
    color: #f1f1f1;
    border: 1px solid #444444;
    font-size: 30px;
    color: #f1f1f1;
}
.sure {
    width: 100%;
    height: 15vh;
    position: absolute;
    top: 0;
    left: 0;
}
.sure p {
    text-align: center;
    margin: 5vh auto;
}
.button-group {
    width: 100%;
    height: 10vh;
    position: absolute;
    top: 15vh;
    left: 0;
}
.yes-bt {
    position: absolute;
    left: 7vw;
    background: none;
    border: none;
    font-size: 30px;
    color: #f1f1f1;
}
.no-bt {
    position: absolute;
    right: 7vw;
    background: none;
    border: none;
    font-size: 30px;
    color: #f1f1f1;
}



