
@font-face {
    font-family: font757;
    src: url("../assets/font/小米兰亭.ttf");
}
body {
    width: 100vw;
    height: 100vh;
    background: url('../assets/user/bg.png') no-repeat center center;
}
.user-title {
    font-family: font757;
    font-size: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 2px;
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#user-manage-container {
    width: 100vw;
    height: 55.556vh;
    position: absolute;
    top: 22.222vh;
    left: 0;
}
#user-manage {
    width: 31.25vw;
    height: 55.556vh;
    margin: 0;
    position: absolute;
    top: 0;
    left: 6.25vw;
}
#profile-photo {
    margin: 0;
    width: 12.5vw;
    height: 55.556vh;
    position: absolute;
    top: 0;
    left: 0;
    background: url('../assets/user/profile_photo.jpg') no-repeat center center;
}
#username-logout {
    width: 18.75vw;
    height: 55.556vh;
    position: absolute;
    margin: 0;
    top: 0;
    left: 12.5vw;
    background: url('../assets/user/bt_bg3.png') no-repeat center center;
}
#username-container {
    width: 18.75vw;
    height: 38.889vh;
    position: absolute;
    top: 0;
    left: 0;
}
#logout-container {
    width: 18.75vw;
    height: 16.667vh;
    position: absolute;
    top: 38.889vh;
    left: 0;
}
#menu-container {
    margin: 0;
    width: 59.375vw;
    height: 55.556vh;
    position: absolute;
    top: 0;
    left: 40.625vw;
}
.username {
    text-align: center;
    margin: 15vh auto 18vh;
    font-family: font757;
    font-size: 3.704vh;
    color: #f1f1f1;
}
#logout {
    border: none;
    background: none;
    font-family: font757;
    font-size: 4.444vh;
    color: #f1f1f1;
    text-align: center;
    margin: 0 auto;
}
#logout:hover {
    cursor: pointer;
}
.fade-enter-active, .fade-leave-active {
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
    opacity: 0
}
.fade-about-enter-active, .fade-about-leave-active {
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}
.fade-about-enter, .fade-about-leave-to /* .fade-leave-active in below version 2.1.8 */ {
    opacity: 0
}
#login-website {
    font-family: font757;
    color: #f1f1f1;
    font-size: 2.963vh;
    position: absolute;
    right: 6.25vw;
    bottom: 6.481vh;
}
