
body {
  width: 100vw;
  height: 100vh;
  background: url('../assets/favorite/bg.png');
  overflow: hidden;
}
.title {
  font-family: font757;
  font-size: 5.556vh;
  color: #f1f1f1;
  letter-spacing: 2px;
  position: absolute;
  top: 6.481vh;
  left: 6.25vw;
}
#favorite-container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  /* border: 1px solid red; */
}
#slide-img-container {
  width: 68.75vw;
  height: 62.964vh;
   /* border: 1px solid red;    */
  position: absolute;
  top: 18.518vh;
  left: 39.586vw;
  overflow: hidden;
}
#slide-img {
  width: 68.75vw;
  height: 59.26vh;
  position: absolute;
   /* border: 1px solid yellow;  */
  top: 1.852vh;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.small-img {
  width: 15.625vw;
  height: 27.78vh;
  /* margin-right: 2.083vw;
    margin-bottom: 3.704vh; */
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}



