
#container {
  width: 500px;
  height: 500px;
}
#outer-box {
  height: 100%;
  padding-right: 300px;
}
#panel {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  overflow: auto;
  width: 300px;
  z-index: 999;
  border-left: 1px solid #eaeaea;
  background: #fff;
}
.clear {
  clear: both;
}
.tipMarker {
  color: #555;
  background-color: rgba(255, 254, 239, 0.8);
  border: 1px solid #7E7E7E;
  padding: 2px 6px;
  font-size: 12px;
  white-space: nowrap;
  display: inline-block;
}
.tipMarker:before,
.tipMarker:after {
  content: '';
  display: block;
  position: absolute;
  margin: auto;
  width: 0;
  height: 0;
  border: solid transparent;
  border-width: 5px 5px;
}
.tipMarker.top {
  -webkit-transform: translate(-50%, -110%);
          transform: translate(-50%, -110%);
}
.tipMarker.top:before,
.tipMarker.top:after {
  bottom: -9px;
  left: 0;
  right: 0;
  border-top-color: rgba(255, 254, 239, 0.8);
}
.tipMarker.top:before {
  bottom: -10px;
  border-top-color: #7E7E7E;
}
#area-tree h2 {
  display: inline-block;
  font-weight: 500;
  font-size: 13px;
  padding: 3px 5px;
  margin: 0;
  border: 1px solid #2ca02c;
  border-width: 1px 3px;
  cursor: pointer;
}
#area-tree h2:hover,
#area-tree h2.hover,
#area-tree h2.selected {
  background: #3366cc;
  color: #fff;
}
#area-tree h2.lv_outside {
  border-color: #666;
  padding: 3px 10px;
}
#area-tree h2.lv_country {
  border-color: #d62728;
  padding: 3px 27px;
  margin: 5px 0;
}
#area-tree h2.lv_province {
  border-color: #ff7f0e;
  padding: 3px 10px;
}
#area-tree h2.lv_district {
  border-color: #dd4477
}
#area-tree ul {
  margin-left: 23px;
  clear: both;
}
#area-tree li {
  float: left;
  margin: 5px 5px 0 0;
  font-size: 12px;
}
#area-tree li.lv_province {
  margin: 5px 5px 5px 0;
}
.showHideBtn {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  vertical-align: middle;
  margin: 0;
  cursor: pointer;
}
.showHideBtn:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  top: 4px;
  left: 4px;
  border: solid rgba(0, 0, 0, 0);
  border-width: 10px 6px;
  border-top-color: #aaa;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
}
.hide-sub .showHideBtn:after {
  -webkit-transform-origin: 50% 20%;
          transform-origin: 50% 20%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.hide-sub > ul {
  display: none;
}



