
#About {
    width: 30vw;
    height: 25vh;
    position: absolute;
    top: 30vh;
    left: 30vw;
    z-index: 100;
    background: url("../assets/user/bt_bg3.png") no-repeat center center;
}
#About .info {
    width: 30vw;
    height: 15vh;
    position: absolute;
    top: 0;
    left: 0;
}
#About p {
    text-align: center;
    margin: 6vh auto;
    color: #f1f1f1;
    font-size: 30px;
}
#About .return-bt {
    width: 30vw;
    height: 10vh;
    position: absolute;
    top: 15vh;
    left: 0;
}
#About button {
    border: none;
    background: none;
    color: #f1f1f1;
    font-size: 30px;
    position: absolute;
    left: 20vw;
}



