
body {
  width: 100vw;
  height: 100vh;
  background: url('../assets/works/bg.png');
  overflow: hidden;
}
.title {
  font-family: font757;
  font-size: 5.556vh;
  color: #f1f1f1;
  letter-spacing: 2px;
  position: absolute;
  top: 6.481vh;
  left: 6.25vw;
}
.photo-amount {
  font-family: font757;
  font-size: 2.222vh;
  color: #f1f1f1;
  letter-spacing: 1px;
  position: absolute;
  top: 14vh;
  left: 6.25vw;
}
#upload {
  width: 6.51vw;
  height: 4.907vh;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 15;
  top: 7.5vh;
  left: 20.677vw;
  background: url("../assets/works/bt_bg2.png") no-repeat center center;
}
#upload:hover {
  background: url("../assets/works/bt_bg3.png") no-repeat center center; 
  cursor: pointer;
}
#upload img {
  width: 2.76vw;
  height: 4.907vh;
  position: absolute;
  top: 0;
  left: 0;
}
#upload span {
  font-family: font757;
  font-size: 20px;
  color: #f1f1f1;
  width: 3.75vw;
  height: 4.907vh;
  text-align: center;
  margin: 13px auto;
  letter-spacing: 1px;
  position: absolute;
  top: 0;
  left: 2.76vw;
}
#works-container {
  width: 100vw;
  height: 100vh;
  /* border: 1px solid red;  */
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}




