
.box {
    width: 15.625vw;
    height: 55.556vh;
    margin: 0;
    position: absolute;
    background: url('../assets/user/bt_bg3.png') no-repeat center center;
    -webkit-transition: all 0.7s;
    transition: all 0.7s;
}
.box:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    position: absolute;
    -webkit-box-shadow: 0px 10px 50px #444444;
            box-shadow: 0px 10px 50px #444444;
}
.box .icon-container {
    width: 100%;
    height: 70%; 
    position: absolute;
    top: 0;
    left: 0;
}
.box .icon-container .img-container {
    width: 45%;
    text-align: center;
    margin: 10vh auto;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}
.box:hover .icon-container .img-container {
    width: 54%;
}
.box div img {
    width: 100%;
}
.box .item-title-container {
    width: 100%;
    height: 30%;
    position: absolute;
    top: 70%;
    left: 0;
}
.box .item-title {
    border: none;
    background: none;
    font-family: font757;
    font-size: 4.444vh;
    color: #f1f1f1;
    text-align: center;
    margin: 0 auto;
    -webkit-transition: 0.7s;
    transition: 0.7s;
}
.box:hover p {
    font-size: 5.333vh;
}



