
.sub-work {
    width: 20.833vw;
    height: 54.833vh;
    position: absolute;
    top: 26.648vh;
}
.time-container {
    width: 100%;
    position: absolute;
    height: 4vh;
    z-index: 5;
}
.upload-time {
    font-family: font757;
    color: #f1f1f1;
    font-size: 2.222vh;
    text-align: center;
    padding: 0 auto;
    z-index: 5;
}
.camera-container {
    width: 100%;
    height: 6.389vh;
    position: absolute;
    z-index: 4;
    margin-top: 4vh;
}
.camera {
    margin-left: 8.594vw;
    width: 3.597vw;
    height: 100%;
    position: absolute;
    z-index: 4;
}
.camera img {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
}
.work-img {
    width: 100%;
    height: 44.444vh;
    position: absolute;
    z-index: 2;
    margin-top: 10.389vh;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    /* border: 1px solid red; */
    /* background: url(../assets/favorite/bg1.jpg) no-repeat center center; */
}
.sub-work .work-img:hover {
    -webkit-transform: scale(1.1,1.083);
            transform: scale(1.1,1.083);
    -webkit-box-shadow: 0 5px 25px #222222;
            box-shadow: 0 5px 25px #222222;
}
.work-img img {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}
.shelter {
    width: 100%;
    height: 7.407vh;
    position: absolute;
    z-index: 2;
    bottom: 0;
    font-family: font757;
    font-size: 2.963vh;
    color: #f1f1f1;
    background-color: rgba(49,71,127,1);
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease
}
.shelter p {
    position: absolute;
    width: 100%;
    z-index: 2;
    text-align: center;
    margin: 20px auto;
}
.work-img:hover .shelter {
    font-size: 3.333vh;
}
.line {
    width: 27.083vw;
    height: 0.185vh;
    position: absolute;
    left: 10.417vw;
    top: 7.195vh;
    z-index: 1;
    border: none;
    border-top: 0.185vh solid #f1f1f1;
}



